const url = 'https://www.googleapis.com/youtube/v3/';
const key = 'AIzaSyAwfVGqB5UZDuOnMlNj3AfVUqIy80xK3f8';

export default class AppModel {
  static async getChunk(valueSearch, page) {
    if (valueSearch === undefined) {
      return undefined;
    }

    let pageToken;
    if (page !== undefined) {
      pageToken = `&pageToken=${page}`;
    } else {
      pageToken = '';
    }

    const response = await fetch(`${url}search?key=${key}&type=video&part=snippet&maxResults=15&q=${valueSearch}${pageToken}`);
    const data = await response.json();

    const info = {
      nextPage: data.nextPageToken,
      items: data.items,
    };

    return info;
  }

  static async getChunkItems(info) {
    if (info === undefined) {
      return undefined;
    }
    const items = [];
    let id = '';

    for (let i = 0; i < info.items.length; i += 1) {
      id += `${info.items[i].id.videoId},`;
    }

    const response = await fetch(`${url}videos?key=${key}&id=${id}&part=snippet,statistics`);
    const data = await response.json();

    for (let i = 0; i < info.items.length; i += 1) {
      const way = {
        snip: data.items[i].snippet,
        stat: data.items[i].statistics,
      };
      const item = {
        url: `https://youtu.be/${info.items[i].id.videoId}`,
        title: way.snip.title,
        picture: way.snip.thumbnails.medium.url,
        description: way.snip.description,
        author: way.snip.channelTitle,
        date: way.snip.publishedAt.split('T')[0],
        views: way.stat.viewCount,
        like: way.stat.likeCount,
        dislike: way.stat.dislikeCount,
      };
      items.push(item);
    }

    return items;
  }
}
