import './scroller.css';
import '../reset.css';

export default class Scroller {
  static draw() {
    const scroller = document.createElement('section');
    scroller.className = 'scroller';
    document.body.appendChild(scroller);

    const scrollerBlock = document.createElement('div');
    scrollerBlock.className = 'scrollerBlock';
    document.querySelector('.scroller').appendChild(scrollerBlock);
  }

  static getItemMargin() {
    let videoOnPage = Math.floor(document.body.offsetWidth / 340);

    if (videoOnPage > 4) {
      videoOnPage = 4;
    }
    const margin = (document.body.offsetWidth - (320 * videoOnPage)) / (2 * videoOnPage);
    const scrollerItem = document.querySelectorAll('.scrollerItem');

    for (let i = 0; i < scrollerItem.length; i += 1) {
      scrollerItem[i].style.margin = `25px ${margin}px`;
    }

    const countInBlock = document.getElementsByClassName('scrollerItem').length;
    const widthBlock = document.querySelector('.scrollerBlock');
    widthBlock.style.width = `${margin * countInBlock * 2 + countInBlock * 320}px`;
    return videoOnPage;
  }

  static fill(items) {
    items.forEach((item) => {
      const record = document.createElement('div');
      record.className = 'scrollerItem';
      document.querySelector('.scrollerBlock').appendChild(record);

      record.style.backgroundImage = `url(${item.picture})`;

      const titleBlock = document.createElement('div');
      titleBlock.className = 'titleItem';
      record.appendChild(titleBlock);

      const urlTitle = document.createElement('a');
      urlTitle.href = item.url;
      urlTitle.textContent = item.title;
      urlTitle.target = '_blank';
      titleBlock.appendChild(urlTitle);

      const infoStatistic = document.createElement('div');
      infoStatistic.className = 'infoStatistic';
      record.appendChild(infoStatistic);

      const authorImg = document.createElement('i');
      authorImg.className = 'fas fa-user';
      infoStatistic.appendChild(authorImg);

      const author = document.createElement('p');
      author.textContent = item.author;
      author.className = 'authorItem';
      author.className = 'itemGridPosition';
      infoStatistic.appendChild(author);

      const dateImg = document.createElement('i');
      dateImg.className = 'far fa-calendar-alt';
      infoStatistic.appendChild(dateImg);

      const date = document.createElement('p');
      date.textContent = item.date;
      date.className = 'dateItem';
      date.className = 'itemGridPosition';
      infoStatistic.appendChild(date);

      const viewsImg = document.createElement('i');
      viewsImg.className = 'far fa-eye';
      infoStatistic.appendChild(viewsImg);

      const views = document.createElement('p');
      views.textContent = item.views;
      views.className = 'viewsItem';
      views.className = 'itemGridPosition';
      infoStatistic.appendChild(views);

      const likeImg = document.createElement('i');
      likeImg.className = 'far fa-thumbs-up';
      infoStatistic.appendChild(likeImg);

      const like = document.createElement('p');
      like.textContent = item.like;
      like.className = 'likeItem';
      infoStatistic.appendChild(like);

      const dislikeImg = document.createElement('i');
      dislikeImg.className = 'far fa-thumbs-down';
      infoStatistic.appendChild(dislikeImg);

      const dislike = document.createElement('p');
      dislike.textContent = item.dislike;
      dislike.className = 'dislikeItem';
      infoStatistic.appendChild(dislike);

      const description = document.createElement('p');
      description.textContent = item.description;
      description.className = 'descriptionItem';
      record.appendChild(description);
    });
  }

  static clear() {
    while (document.querySelectorAll('.scrollerBlock').length > 1) {
      document.querySelector('.scrollerBlock').remove();
    }
    while (document.querySelectorAll('.scrollerBlock .scrollerItem').length > 0) {
      document.querySelector('.scrollerItem').remove();
    }
  }
}
