import Error from '../views/error/error';
import AppModel from '../models/AppModel';
import Header from '../views/header/header';
import Scroller from '../views/scroller/scroller';
import Pagination from '../views/pagination/pagination';

function init() {
  Header.draw();
  Scroller.draw();
  Pagination.draw();
}

init();

const debounce = require('lodash/debounce');

const paginationNext = document.querySelector('.next');
const paginationPrev = document.querySelector('.prev');
const paginationPrevPrev = document.querySelector('.prevprev');
const slider = document.querySelector('.scroller');
let startX;
let scrollWidth;
let videoOnPage;
let valueSearch;
let info;

async function setValueSearch() {
  valueSearch = document.querySelector('input').value.trim();
}

async function setPagination() {
  await new Promise(resolve => setTimeout(resolve, 1000));
  Pagination.drawPaginationItem(Math.floor(slider.scrollLeft / scrollWidth) + 1);
}

async function search(event) {
  event.preventDefault();
  Error.clear();
  setValueSearch();
  Scroller.clear();
  Pagination.paginationClear();
  info = await AppModel.getChunk(valueSearch);
  const items = await AppModel.getChunkItems(info);
  if (items.length === 0) {
    Error.draw();
  } else {
    Scroller.fill(items);
    videoOnPage = Scroller.getItemMargin();
    scrollWidth = document.body.offsetWidth;
    setPagination();
  }
}

async function getNextPage() {
  if (info.nextPage !== undefined) {
    info = await AppModel.getChunk(valueSearch, info.nextPage);
    const items = await AppModel.getChunkItems(info);
    Scroller.fill(items);
    Scroller.getItemMargin();
  }
}

function checkNextPage() {
  const lastItem = document.querySelector('.scrollerBlock').lastChild;
  const lastRight = lastItem.getBoundingClientRect().right;
  const sliderRight = slider.getBoundingClientRect().right;
  if (lastRight - sliderRight * 2 < scrollWidth) {
    getNextPage();
  }
}

async function checkSizePage() {
  const sliderBlock = document.querySelector('.scrollerBlock');
  const sliderLeft = sliderBlock.getBoundingClientRect().left;
  const diff = sliderLeft % scrollWidth;
  if (diff !== 0) {
    if (Math.abs(diff) < scrollWidth / 2) {
      slider.scrollBy(-Math.abs(diff), 0);
    } else {
      slider.scrollBy(scrollWidth - Math.abs(diff), 0);
    }
  }
  setPagination();
}
const checkSizePages = debounce(checkSizePage, 1000);

function scrollPage(n) {
  slider.scrollBy(n * scrollWidth, 0);
  checkNextPage();
  checkSizePages();
  setPagination();
}

slider.addEventListener('pointerdown', (event) => {
  startX = event.pageX - slider.offsetLeft;
});

slider.addEventListener('pointerup', (event) => {
  const currentX = event.pageX - slider.offsetLeft;

  if (currentX - 30 > startX) {
    scrollPage(-1);
  }
  if (currentX + 30 < startX) {
    scrollPage(1);
  }
});

slider.addEventListener('touchstart', (event) => {
  startX = event.touches[0].screenX;
});

slider.addEventListener('touchend', (event) => {
  const currentX = event.changedTouches[0].screenX;

  if (currentX - 20 > startX) {
    scrollPage(-1);
  }
  if (currentX + 20 < startX) {
    scrollPage(1);
  }
});

async function resize() {
  const pageNumber = +document.querySelector('.paginationNumber').textContent;
  const number = pageNumber * videoOnPage - (videoOnPage - 1);

  videoOnPage = await Scroller.getItemMargin();
  scrollWidth = await document.body.offsetWidth;
  const page = Math.ceil(number / videoOnPage);

  const length = scrollWidth * (page - 1);
  document.querySelector('.scroller').scrollLeft = length;

  await new Promise(resolve => setTimeout(resolve, 1000));
  checkSizePage();
}

paginationNext.addEventListener('click', () => { scrollPage(1); });
paginationPrev.addEventListener('click', () => { scrollPage(-1); });
paginationPrevPrev.addEventListener('click', () => { scrollPage(-2); });

window.addEventListener('resize', debounce(resize, 500));
document.addEventListener('submit', search);
